.cruises {
  padding-left: 25px;
  padding-right: 25px;

  @media (min-width: $desktop-width) {
    padding-left: 118px;
    padding-right: 118px;
  }

  @media (min-width: $tablet-width) {
    padding-left: 60px;
    padding-right: 60px;
  }
}

.cruises__title {
  margin: 0;
  text-align: center;
  margin-bottom: 60px;
}

.cruises__list {
  display: block;
  padding: 0;
  margin: 0;
  list-style-type: none;

  @media (min-width: $desktop-width) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  @media (min-width: $tablet-width) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}

.cruises__img-title {
  font-family: $font-antarctic;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 150%;
  align-items: center;
  color: #f9fbfd;
  margin-top: -60px;
  padding-left: 30px;

  @media (min-width: $desktop-width) {
    margin-top: -60px;
    padding-left: 30px;
  }

  @media (min-width: $tablet-width) {
    margin-top: -60px;
    padding-left: 30px;
  }
}

.cruises__item {
  position: relative;
  margin-bottom: 55px;

  @media (min-width: $desktop-width) {
    margin-bottom: 30px;
  }

  @media (min-width: $tablet-width) {
    margin-bottom: 30px;
  }
}

.cruises__page-list {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

.cruises__page-title {
  margin: 0;
  padding: 0;
  font-family: $font-antarctic;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 150%;
  align-items: center;
  color: #f9fbfd;
  margin-bottom: 15px;

  @media (min-width: $desktop-width) {
    margin-bottom: 15px;
  }

   @media (min-width: $tablet-width) {
    margin-bottom: 15px;
  }
}

.cruises__page-text {
  font-family: $font-antarctic;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 140%;
  align-items: center;
  color: #d8d8d9;
  margin: 0;
  padding: 0;
  margin-bottom: 16px;

  @media (min-width: $desktop-width) {
    margin-bottom: 16px;
  }

  @media (min-width: $tablet-width) {
    margin-bottom: 16px;
  }
}

.cruises__page-list-title {
  font-family: $font-antarctic;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 150%;
  align-items: center;
  color: #f9fbfd;
}

.cruises__page-list-title {
  margin: 0;
  padding: 0;

  @media (min-width: $desktop-width) {
    margin-bottom: 5px;
  }
}

.cruises__img-wrapper {
  &--back {
    display: none;                                                  //Отключение форм!!!
    background: #011c40;
  }

  @media (min-width: $desktop-width) {
    &--back {
      position: absolute;
      top: 1px;
      left: 1px;
      padding: 15px 25px 0 25px;
      width: 79.6%;
      min-height: 369px;
    }
  }

  @media (min-width: $tablet-width) {
    &--back {
      position: absolute;
      top: 1px;
      left: 1px;
      padding: 25px 25px 35px 25px;
      width: 83.2%;
      min-height: 325px;
    }
  }

  @media (min-width: $mobile-width) {
    &--back {
      position: absolute;
      top: 1px;
      left: 1px;
      padding: 25px 25px 35px 25px;
      height: 313px;
      width: 81.3%;                    //Отключение форм!!!
    }
  }


}

.cruises__img-wrapper img {
    width: 270px;
    height: 386px;

  @media (min-width: $tablet-width) {
    width: 309px;
    height: 386px;
  }

  @media (min-width: $desktop-width) {
    width: 260px;
    height: 386px;
  }
}

.cruises__img-link {
  font-family: $font-antarctic;
  font-style: normal;
  align-items: center;
  font-weight: 600;
  font-size: 14px;
  line-height: 150%;
  color: #011c40;
  text-decoration: none;
  padding: 14px 51px 15px 52px;
  background-color: #f9fbfd;

  @media (min-width: $desktop-width) {
    text-decoration: none;
    padding: 14px 46px 15px 47px;
    background-color: #f9fbfd;
  }

  @media (min-width: $tablet-width) {
    text-decoration: none;
    padding: 14px 73px 15px 73px;
    background-color: #f9fbfd;
    margin: 0 auto;
  }
}
